.ham {
	width: 30px;
	height: 30px;
	position: relative;
	cursor: pointer;
	border: 1px solid $primary-red-color;
	div {
		position: absolute;
		left: 50%;
		width: 20px;
		height: 2px;
		background: $primary-red-color;
		top: 50%;
		transform: translate(-50%, -50%);
		transition: all .3s ease-in-out;
		&:before, &:after {
			width: 100%;
			height: 2px;
			background: $primary-red-color;
			position: absolute;
			content: '';
			top: -7px;
			transition: all .3s ease-in-out; }
		&:after {
			top: auto;
			bottom: -7px; } }

	&:not(.collapsed) {
		div {
			background: none;
			&:before {
				top: 0;
				transform: rotate(45deg); }
			&:after {
				bottom: 0;
				transform: rotate(-45deg); } } } }




.site-header {
	.top {
		position: relative;
		padding: 15px 0;
		.call {
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			display: flex;
			align-items: center;
			.ic {
				width: 40px;
				height: 40px;
				border-radius: 50%;
				background: $primary-yellow-color;
				color: #fff;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 10px; }
			p {
				margin-bottom: 0; }
			h6 {
				margin-bottom: 0;
				font-size: 24px; } }
		.logo {
			display: table;
			margin-left: auto;
			margin-right: auto; }
		.search {
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			form {
				margin-bottom: 0;
				width: 270px;
				display: flex;
				align-items: center;
				border: 1px solid  #dddddd;
				display: flex;
				border-radius: 20px;
				input {
					border: none;
					background: none;
					font-size: 14px;
					flex-grow: 1;
					height: 35px;
					padding: 0 15px; }
				button {
					background: none;
					border: none;
					width: 35px;
					height: 35px;
					font-size: 14px;
					color: $primary-yellow-color; } } } }
	.top-hr {
		width: 100%;
		height: 2px;
		background: #ddd; }


	.nav {
		border-bottom: 1px solid #dddddd;
		ul {
			display: flex;
			justify-content: center;
			> li {
				> a {
					display: block;
					font-size: 14px;
					text-transform: uppercase;
					color: #4f4f4f;
					font-weight: 500;
					padding: 25px 12px;
					&:hover, .active {
						color: $primary-red-color; } } } } } }


.site-header-mobile {
	background: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 15px;
	z-index: 10;
	position: relative;
	padding: 10px 10px;
	border-bottom: 1px solid rgba(black, .5);
	.logo {
		max-width: 200px;
		img {
			max-width: 100%; } }

	.search-form {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid rgba(grey, .5);
		form {
			display: flex;
			padding: 15px;
			// max-width: 500px
			margin-left: auto;
			margin-right: auto;
			input {
				flex-grow: 1;
				border: 1px solid $primary-yellow-color;
				border-top-left-radius: 30px;
				border-bottom-left-radius: 30px;
				height: 40px;
				text-align: center;
				padding: 0 10px; }

			button {
				border: 0;
				padding: 0 20px;
				border-top-right-radius: 30px;
				border-bottom-right-radius: 30px;
				background: $primary-yellow-color;
				&:hover {
					background: $primary-red-color;
					color: #fff; } } } }

	.mobile-nav {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%; }

	.right {
		display: flex;
		align-items: center;
		.search {
			display: block;
			width: 30px;
			height: 30px;
			color: $primary-yellow-color;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 15px;
			border: 1px solid $primary-yellow-color;
			font-size: 13px;
			position: relative;
			.fa-2 {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				opacity: 0; }
			.fa-1 {
				opacity: 1; }
			&:not(.collapsed) {
				.fa-1 {
					opacity: 0; }
				.fa-2 {
					opacity: 1; } } }
		.ham {
			display: block; } }


	.mobile-nav {
		background: #fff;
		border-top: 1px solid rgba($primary-red-color, .3);

		ul {
			padding-top: 10px;
			padding-bottom: 10px;
			> li {
				> a {
					font-size: 13px;
					text-transform: uppercase;
					color: $primary-red-color;
					display: table;
					margin-left: auto;
					margin-right: auto;
					padding: 10px 0;
					&:hover {
						color: #333; } } } } } }






