.site-footer {
	.logo {
		margin-top: 77px;
		margin-bottom: 30px;
		img {
			max-width: 100%; } } }
.footer-block {
	h6 {
		font-weight: 300;
		color: $primary-red-color;
		margin-bottom: 50px; } }

.footer-contact-info {
	display: flex;
	align-items: center;
	margin-bottom: 25px;
	.ic {
		width: 27px;
		height: 27px;
		border-radius: 2px;
		background: $primary-red-color;
		color: #fff;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		margin-right: 14px;
		.material-icons {
			font-size: 16px; } }

	p {
		color: $primary-red-color;
		margin-bottom: 0;
		line-height: 1.5; } }

.footer-ul {
	> li {
		margin-bottom: 10px;
		> a {
			font-size: 14px;
			color: $primary-red-color;
			display: table;
			position: relative;
			padding-left: 25px;
			&:before {
				width: 10px;
				height: 10px;
				border-radius: 50%;
				content: '';
				border: 2px solid $primary-red-color;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%); }
			&:hover {
				color: #fff; } } } }


@media (max-width: 991.82px) {
	.site-footer .logo {
		margin-top: 0; }

	.footer-block {
		h6 {
			margin-bottom: 20px; } } }



