
.page-banner-item {
	height: 450px;
	position: relative;
	.text {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(-50%);
		text-align: center;
		h5 {
			font-size: 64px;
			color: $primary-yellow-color;
			font-weight: 900;
			text-transform: uppercase;
			line-height: 1;
			margin-bottom: 0; }
		p {
			color: $primary-yellow-color;
			font-size: 30px;
			font-weight: bold;
			text-transform: uppercase;
			line-height: 1;
			margin-bottom: 30px; }
		.btn-group {
			display: flex;
			justify-content: center;
			> * {
				margin-left: 10px;
				margin-right: 10px; } } } }

.swiper-nav-btn {
	display: block;
	width: 30px;
	height: 30px;
	border: 1px solid #fff;
	border-radius: 2px;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	.material-icons {
		font-size: 18px; }
	&:focus {
		outline: 0; }
	&:hover {
		background: #fff;
		color: $primary-yellow-color; } }


.page-banner-nav {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 1;
	&.next {
		right: 10px; }
	&.prev {
		left: 10px; } }


.ul-boxs {
	display: flex;
	flex-wrap: wrap;
	> li {
		width: 25%;
		border-left: 1px solid #e0e0e0;
		display: flex;
		&:last-child {
			border-right: 1px solid #e0e0e0; } } }

.ul-box {
	text-align: center;
	padding: 47px 0;
	display: block;
	transition: all .3s ease-in-out;
	width: 100%;
	.material-icons {
		font-size: 48px;
		color: rgba(#676767, .5);
		transition: all .3s ease-in-out; }
	.ic {
		margin-bottom: 18px; }
	p {
		font-size: 18px;
		font-weight: 500;
		margin-bottom: 0;
		color: #fff; }

	&:hover, &.active {
		background: $primary-yellow-color;
		.material-icons {
			color: #fff; } } }

.home-section-1 {
	img {
		position: absolute;
		bottom: 0;
		right: 10%;
		max-height: 95%; }
	.text {
		// max-width: 775px
		text-align: center;
		p {
			margin-bottom: 50px;
			&:last-child {
				margin-bottom: 0; }
			&.c-1 {
				color: #302525; }
			&.c-2 {
				color: black; } }

		.site-btn {
			margin-left: auto;
			margin-right: auto; } } }

.ul-boxs-2 {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap; }
.ul-box-2 {
	display: block;
	text-align: center;
	padding: 35px 0;
	.material-icons {
		font-size: 36px;
		color: #fff; }
	.ic {
		margin-bottom: 20px; }
	h6 {
		font-size: 36px;
		color: $primary-yellow-color;
		font-weight: 500;
		margin-bottom: 20px; }
	p {
		margin-bottom: 0;
		font-size: 18px;
		color: #fff; } }



.home-qa-form {
	background: $primary-yellow-color;
	padding-left: 30px;
	padding-right: 30px;
	.title__ {
		margin-bottom: 30px;
		h3 {
			margin-bottom: 20px;
			color: #fff; }
		p {
			color: #fff;
			font-weight: 300;
			font-size: 24px;
			margin-bottom: 0; } } }


.home-qa-text {
	padding-left: 40px;
	.title__wrap {
		display: table; }

	.site-btn {
		margin-top: 50px; } }



.home-mAppointment-section {
	.pick-a-date {
		margin-top: 30px;
		display: flex;
		flex-wrap: wrap;
		.form-group {
			max-width: 270px;
			padding-left: 10px;
			padding-right: 10px; } } }


.home-practice-area-section {
	.see-more {
		margin-top: 40px; } }



.home-make-document-section {
	.big-search {
		margin-bottom: 25px; }
	.step-arrow-component {
		margin-bottom: 45px; }

	h4 {
		color: $primary-red-color;
		margin-bottom: 35px; } }

.need-to-do {
	.i-t {
		display: flex;
		white-space: nowrap;
		color: #fff;
		font-size: 21px;
		align-items: center;
		margin-bottom: 40px;
		img {
			width: 27px;
			height: 37px;
			flex-shrink: 0;
			max-width: 100%;
			object-fit: contain;
			margin-right: 5px; } } }


.hashtag-tab {
	.nav {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;
		> li {
			padding-left: 10px;
			padding-right: 10px;
			> a {
				font-size: 16.8px;
				color: $primary-red-color;
				font-weight: bold;
				text-transform: uppercase;
				&.active {
					color: $primary-yellow-color; } } } } }

.home-blog-section {
	.hashtag-tab {
		.nav {
			margin-bottom: 35px; } }

	.blog-row {
		> * {
			margin-bottom: 35px; } }

	.site-btn {
		padding-left: 55px;
		padding-right: 55px; } }

@media (max-width: 1199.82px) {
	.step-arrow-component {
		.nav>li {
			a {
				padding-right: 33px; } } } }

@media (max-width: 991.82px) {
	.page-banner-item {
		.text {
			h5 {
				font-size: 40px; }
			p {
				font-size: 20px; } } }

	.home-qa-text {
		padding-left: 0;
		.title__wrap {
			margin-left: auto;
			margin-right: auto; } }


	.step-arrow-component {
		.nav {
			>li {
				a {
					height: 60px;
					line-height: 60px;
					font-size: 20px;
					&:before {
						border-width: 30px; } } } } } }
@media (max-width: 767.82px) {

	.ul-boxs {
		>li {
			width: 50%;
			border: none!important; } }

	.ul-boxs-2 {
		> li {
			width: 50%; } }


	.home-mAppointment-section {
		.pick-a-date {
			.form-group {
				width: 50%; }

			.site-btn {
				margin-left: auto;
				margin-right: auto; } } }


	.step-arrow-component {
		.nav {
			>li {
				a {
					padding-right: 15px; } } } }



	.home-make-document-section {
		h4 {
			font-size: 24px; } } }

@media (max-width: 575.82px) {

	.page-banner-item {
		.text {
			h5 {
				font-size: 30px; }
			p {
				font-size: 17px; } }

		.btn-group {
			flex-direction: column;
			align-items: center;
			.site-btn {
				max-width: 200px;
				white-space: nowrap;
				margin-bottom: 10px; } } }

	.home-practice-area-section {
		.see-more {
			margin-top: 0; } }


	.home-make-document-section {
		h4 {
			font-size: 25px; } }

	.need-to-do-row {
		.i-t {
			justify-content: center; } } }
@media (max-width: 500px) {

	.ul-boxs>li {
		width: 100%; }
	.ul-boxs-2>li {
		width: 100%; }


	.home-mAppointment-section {
		.pick-a-date {
			flex-direction: column;
			align-items: center;
			.form-group {
				width: 50%; } } }


	.step-arrow-component .nav>li {
		a {
			height: 30px;
			line-height: 30px;
			font-size: 15px;
			padding-right: 0;
			text-align: center;
			&:before {
				border-width: 15px; } } } }



